/** @jsxImportSource @emotion/react */
import React, { useImperativeHandle, useEffect, forwardRef, useState, useRef } from 'react'
import { ICellRendererParams, ICellEditorParams } from 'ag-grid-community'
import AddIcon from '@mui/icons-material/AddCircleOutline'
import DeleteIcon from '@mui/icons-material/Delete'
import FilterAltIcon from '@mui/icons-material/FilterAlt'
import IconButton from '@mui/material/IconButton'

import { OrganismsDataGridContext, OrganismRow } from './SampleTaxonomyIdentification'
import { Organism } from '../../types'

interface CellRendererParams<T> extends ICellRendererParams {
  context: OrganismsDataGridContext
  data: OrganismRow
  value: T
}
interface CellEditorParams<T> extends ICellEditorParams {
  context: OrganismsDataGridContext
  data: OrganismRow
  value: T
}

export const NumberEditor = forwardRef(
  (
    {
      value: oldValue,
      precisionAllowed = 0,
      negativeAllowed = false,
    }: CellEditorParams<number> & { precisionAllowed: number; negativeAllowed?: boolean },
    ref
  ) => {
    const [value, setValue] = useState(oldValue || '')
    const inputRef = useRef<HTMLInputElement | null>(null)

    useEffect(() => {
      setTimeout(() => inputRef.current?.focus())
    }, [])

    useImperativeHandle(ref, () => {
      return {
        getValue: () => value,
        isCancelBeforeStart: () => false,
        isCancelAfterEnd: () => false,
        isPopup: () => false,
      }
    })

    const handleChange = ({ target: { value: newValue } }: React.ChangeEvent<HTMLInputElement>) => {
      const newNumber = Number(newValue)
      if (Number(newNumber.toFixed(precisionAllowed)) !== newNumber) return
      if (!negativeAllowed && newNumber < 0) return
      setValue(newNumber)
    }

    return (
      <input
        // className="ag-input"
        type="number"
        ref={inputRef}
        value={value}
        onChange={handleChange}
        style={{ width: '100%', textAlign: 'right' }}
      />
    )
  }
)
NumberEditor.displayName = 'NumberEditor'

export const LifeStageEditor = forwardRef(
  ({ value: oldValue, data: { allowedLifeStages } }: CellEditorParams<number>, ref) => {
    const [value, setValue] = useState(oldValue)
    const [done, setDone] = useState(false)

    const inputRef = useRef<HTMLSelectElement | null>(null)

    const handleChange = ({ target: { value: newValue } }: React.ChangeEvent<HTMLSelectElement>) => {
      setValue(Number(newValue))
      setDone(true)
    }
    useEffect(() => {
      if (!done) return
      // api.stopEditing();
    }, [done])

    useEffect(() => {
      setTimeout(() => inputRef.current?.focus())
    }, [])

    useImperativeHandle(ref, () => {
      return {
        getValue: () => Number(value),
        isCancelBeforeStart: () => false,
        isCancelAfterEnd: () => false,
        isPopup: () => false,
      }
    })

    const options = allowedLifeStages.map(({ id: value, name: label }) => ({ value, label }))

    if (!value) options.unshift({ value: -1, label: '' })

    return (
      <select value={value} onChange={handleChange} ref={inputRef} style={{ width: '100%' }}>
        {options.map(({ value, label }) => (
          <option key={value} value={value}>
            {label}
          </option>
        ))}
      </select>
    )
  }
)
LifeStageEditor.displayName = 'LifeStageEditor'

export const LifeStageRenderer = ({ context, value }: CellRendererParams<number>): React.ReactNode => {
  return context.lifeStages.find(({ id }) => value === id)?.name || ''
}

export const NumberRenderer = ({ value }: CellRendererParams<number>): React.ReactNode => {
  return value || ''
}

const NoteFlagBadge = ({ abbreviation }: { abbreviation?: string }) => (
  <span
    css={{
      marginRight: '0.2em',
      background: '#cc7000',
      color: 'white',
      height: '1.2em',
      padding: '0.1em 0.2em',
    }}
  >
    <span
      css={{
        width: '1em',
        display: 'inline-block',
        textAlign: 'center',
      }}
    >
      {abbreviation}
    </span>
  </span>
)

export const NotesRenderer = ({
  value: { text, flagIds },
  context: { noteTypes },
}: CellRendererParams<Organism['notes']>): React.ReactNode => {
  return (
    <div>
      {flagIds.map((flagId) => (
        <NoteFlagBadge key={flagId} abbreviation={noteTypes.find(({ id }) => id === flagId)?.abbreviation || '?'} />
      ))}
      <span>{text}</span>
    </div>
  )
}

export const NotesEditor = forwardRef(
  ({ value: oldValue, context: { noteTypes } }: CellEditorParams<Organism['notes']>, ref) => {
    const [text, setText] = useState(oldValue.text)
    const [flagIds, setFlagIds] = useState(oldValue.flagIds)

    const inputRef = useRef<HTMLTextAreaElement | null>(null)

    useEffect(() => {
      setTimeout(() => inputRef.current?.focus())
    }, [])

    useImperativeHandle(ref, () => {
      return {
        getValue: () => ({
          text,
          flagIds,
        }),
        isCancelBeforeStart: () => false,
        isCancelAfterEnd: () => false,
        isPopup: () => true,
      }
    })

    // TODO: limit to active note types

    return (
      <div css={{ minWidth: '20em' }}>
        <textarea
          ref={inputRef}
          value={text}
          onChange={(event) => setText(event.target.value)}
          css={{ width: '95%', height: '3em', fontFamily: 'Roboto' }}
        />
        <div>
          {noteTypes.map(({ id, name, abbreviation }) => (
            <div key={id} css={{ margin: '0.2em 0' }}>
              <input
                type="checkbox"
                checked={flagIds.includes(id)}
                onChange={(e) => {
                  if (e.target.checked) {
                    setFlagIds(() => [...flagIds, id])
                  } else {
                    setFlagIds(() => flagIds.filter((flagId) => flagId !== id))
                  }
                }}
              />
              (<span css={{ fontWeight: 'bold' }}>{abbreviation}</span>) - {name}
            </div>
          ))}
        </div>
      </div>
    )
  }
)
NotesEditor.displayName = 'NotesEditor'

