export const ActionsRenderer = ({
  data: organismRow,
  context: { actions, setDeleteCandidateOrganism },
}: CellRendererParams<OrganismRow>): React.ReactNode => {
  const { splitCount, bigRareCount, notes, canDuplicate, canFilter, canDelete } = organismRow
  return (
    <div css={{ display: 'flex', alignItems: 'center', height: '100%' }}>
      <IconButton
        disabled={!canDuplicate}
        onClick={() => {
          actions.duplicateOrganism(organismRow)
        }}
      >
        <AddIcon />
      </IconButton>
      <IconButton
        disabled={!canFilter}
        onClick={() => {
          actions.addFilter(organismRow)
        }}
      >
        <FilterAltIcon />
      </IconButton>
      <IconButton
        disabled={!canDelete}
        onClick={() => {
          if (!splitCount && !bigRareCount && !notes.text && notes.flagIds.length === 0) {
            actions.deleteOrganism(organismRow)
          } else {
            setDeleteCandidateOrganism(organismRow)
          }
        }}
      >
        <DeleteIcon />
      </IconButton>
    </div>
  )
}
