/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'

import { PageAccessField } from '../PageAccessField'
import { signOutState } from '../../data/api'

export const AppHeader = (): JSX.Element => {
  const signOut = useRecoilValue(signOutState)

  return (
    <header css={{ height: '3em', background: '#ccc', display: 'flex', alignItems: 'center' }}>
      <h1 css={{ fontWeight: 'bold', fontSize: '200%' }}>Instar</h1>
      <a
        css={{ marginLeft: '1em' }}
        href="#"
        onClick={(e) => {
          e.preventDefault()
          signOut()
        }}
      >
        Sign Out
      </a>
      <div css={{ margin: '0 0.25em 0 auto' }}>
        <PageAccessField />
      </div>
    </header>
  )
}
