/** @jsxImportSource @emotion/react */
import React, { useState } from 'react'
import MuiAccordion from '@mui/material/Accordion'
import MuiAccordionSummary from '@mui/material/AccordionSummary'
import MuiAccordionDetails from '@mui/material/AccordionDetails'
import MuiButton from '@mui/material/Button'
import Skeleton from '@mui/material/Skeleton'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import EditIcon from '@mui/icons-material/Edit'
import StopIcon from '@mui/icons-material/Stop'

import { Sample } from '../../types'

interface SampleInfoPanelProps {
  sample?: Sample
  isEditing: boolean
  setIsEditing: (value: boolean) => void
}

const GridInfoField = ({ name, label, value }: { name: string; label: string; value: React.ReactNode }) => (
  <>
    <div css={{ gridArea: `${name}Label` }}>{label}:</div>
    <div css={{ gridArea: `${name}Value` }}>{value}</div>
  </>
)

export const InfoPanel: React.FC<SampleInfoPanelProps> = ({
  sample,
  isEditing,
  setIsEditing,
}: SampleInfoPanelProps) => {
  const [isSummaryExpanded, setIsSummaryExpanded] = useState(true)

  if (!sample) return <Skeleton variant="rectangular" />

  const { id, boxId, sorterName = '', sampleDate, sortDate, customerName, siteName } = sample

  return (
    <MuiAccordion
      expanded={isSummaryExpanded}
      onChange={(e, newValue) => setIsSummaryExpanded(newValue)}
      css={{ marginBottom: '1em' }}
    >
      <MuiAccordionSummary expandIcon={<ExpandMoreIcon />} css={{ display: 'flex' }}>
        <span css={{ alignSelf: 'center' }}>
          Box {boxId}, Sample {id}
        </span>
        <div css={{ margin: '0 0.5em 0 auto' }}>
          <MuiButton
            onClick={(e) => {
              e.stopPropagation()
              setIsEditing(!isEditing)
            }}
            startIcon={isEditing ? <StopIcon /> : <EditIcon />}
          >
            {isEditing ? 'Stop Identification' : 'Start Identification'}
          </MuiButton>
        </div>
      </MuiAccordionSummary>
      <MuiAccordionDetails
        css={{
          display: 'grid',
          gap: '0.75em',
          gridTemplateColumns: 'repeat(4, auto) 1fr',
          gridTemplateAreas: `
          "sampleDateLabel sampleDateValue . . ."
            "sorterLabel sorterValue sortDateLabel sortDateValue ."
            "customerLabel customerValue siteLabel siteValue ."
        `,
        }}
      >
        <GridInfoField name="sorter" label="Sorter" value={sorterName} />
        <GridInfoField name="sortDate" label="Sort Date" value={sortDate} />
        <GridInfoField name="sampleDate" label="Sample Date" value={sampleDate} />
        <GridInfoField name="customer" label="Customer" value={customerName} />
        <GridInfoField name="site" label="Site" value={siteName} />
      </MuiAccordionDetails>
    </MuiAccordion>
  )
}
