import React, { useState } from 'react'
import MuiTextField from '@mui/material/TextField'

// import { useAppRoute } from '../AppRouter'

interface PageAccessFieldProps {
  className?: string
}

export const PageAccessField = ({ className }: PageAccessFieldProps): JSX.Element => {
  const [value, setValue] = useState('')
  // const navigateTo = useAppRoute()

  const handleKeyDown = ({ key }: React.KeyboardEvent<HTMLInputElement>): void => {
    if (key === 'Enter') {
      // navigateTo.sampleTaxonomyIdentification({ sampleId: value })
    }
  }

  return (
    <MuiTextField
      className={className}
      size="small"
      value={value}
      onChange={({ target: { value: newValue } }) => setValue(newValue)}
      InputProps={{
        onKeyDown: handleKeyDown,
      }}
      placeholder="Find"
    />
  )
}
