/** @jsxImportSource @emotion/react */
import React, { useEffect } from 'react'
import { HashRouter as ReactRouter } from 'react-router-dom'
import { RecoilRoot, useRecoilValue, useSetRecoilState } from 'recoil'

import { AppRouter } from './components/AppRouter'
import { GraphApi, apiState, signOutState } from './data/api'

import './normalize.css'
import './App.css'

export interface AppProps {
  signOut: () => void
}

const apiInstance = new GraphApi()

const Component = ({ signOut }: AppProps): JSX.Element => {
  const setApi = useSetRecoilState(apiState)
  const setSignOutState = useSetRecoilState(signOutState)

  useEffect(() => {
    setApi(apiInstance)
  }, [])

  useEffect(() => {
    setSignOutState(() => signOut)
  }, [signOut])

  if (useRecoilValue(apiState) !== apiInstance) return <div /> // wait until API is set

  return (
    <ReactRouter>
      <AppRouter />
    </ReactRouter>
  )
}

export const App = (props: AppProps): JSX.Element => {
  return (
    <RecoilRoot>
      <Component {...props} />
    </RecoilRoot>
  )
}
