import { useReducer, useMemo } from 'react'
import produce from 'immer'

import { Organism, Sample, Taxon } from '../../types'
import { PresentableTaxonOption } from '../TaxonomyField'
import { useApi } from '../../data/api'

type ReducerActionType =
  | 'loadSample'
  | 'addOrganism'
  | 'deleteOrganism'
  | 'duplicateOrganism'
  | 'addFilter'
  | 'deleteFilter'
  | 'setOrganismFieldValue'

export interface OrganismFilter {
  taxonId: number
}

export interface OrganismFieldChangedEvent {
  id: number
  field: keyof Organism
  value: any
}

type EditSampleState = {
  organisms: Organism[]
  organismFilters: OrganismFilter[]
  isBugSizeRequired: boolean
}

const uniqueId = (() => {
  let id = 0
  return () => id++
})()

export const initialState = {
  organisms: [],
  organismFilters: [],
  isBugSizeRequired: false,
} as EditSampleState

export const editSampleReducer = (
  state: EditSampleState,
  action: { type: ReducerActionType; payload: any }
): EditSampleState => {
  return produce(state, (draft) => {
    console.log('dispatched:', action)

    switch (action.type) {
      case 'loadSample': {
        const { payload: sample }: { payload: Sample } = action
        draft.organisms = sample.organisms
        draft.isBugSizeRequired = sample.isBugSizeRequired
        break
      }
      case 'addOrganism': {
        const { payload: taxon }: { payload: Taxon } = action
        draft.organisms.push({
          id: uniqueId(), // TODO: probably needs to be a temporary ID until server response returns
          taxonId: taxon.id,
          splitCount: 0,
          notes: { text: '', flagIds: [] },
        })
        break
      }
      case 'duplicateOrganism': {
        const { payload: organism }: { payload: Organism } = action
        draft.organisms.push({
          id: uniqueId(), // TODO: probably needs to be a temporary ID until server response returns
          taxonId: organism.taxonId,
          splitCount: 0,
          notes: { text: '', flagIds: [] },
        })
        break
      }
      case 'addFilter': {
        const {
          payload: { taxonId },
        } = action
        if (state.organismFilters.find((existingFilter) => existingFilter.taxonId === taxonId)) return
        draft.organismFilters.push({ taxonId })
        break
      }
      case 'deleteFilter': {
        const {
          payload: { taxonId },
        } = action
        draft.organismFilters = draft.organismFilters.filter((organismFilter) => organismFilter.taxonId !== taxonId)
        break
      }
      case 'deleteOrganism': {
        const {
          payload: { id },
        } = action
        draft.organisms = draft.organisms.filter((organism) => organism.id !== id)
        break
      }
      case 'setOrganismFieldValue': {
        const { payload }: { payload: OrganismFieldChangedEvent } = action
        const organism = draft.organisms.find((organism) => organism.id === payload.id)
        if (!organism) return
        organism[payload.field] = payload.value
        break
      }
    }
    return
  })
}

export interface EditSampleActions {
  addOrganism: (payload: PresentableTaxonOption) => void
  deleteOrganism: (payload: { id: number }) => void
  duplicateOrganism: (payload: Organism) => void
  addFilter: (payload: { taxonId: number }) => void
  deleteFilter: (payload: { taxonId: number }) => void
  setOrganismFieldValue: (payload: OrganismFieldChangedEvent) => void
  loadSample: (payload: Sample) => void
}

export const useEditSampleReducer = (): [EditSampleState, EditSampleActions] => {
  const api = useApi()

  const [state, dispatch] = useReducer(editSampleReducer, initialState)
  const actions = useMemo(
    () => ({
      addOrganism: (payload: PresentableTaxonOption) => {
        api.createOrganism({
          taxonId: 1,
          splitCount: 0,
          notes: {
            text: '',
            flagIds: [],
          },
        })
        dispatch({ type: 'addOrganism', payload })
      },
      duplicateOrganism: (payload: Organism) => dispatch({ type: 'duplicateOrganism', payload }),
      deleteOrganism: (payload: { id: number }) => dispatch({ type: 'deleteOrganism', payload }),
      addFilter: (payload: { taxonId: number }) => dispatch({ type: 'addFilter', payload }),
      deleteFilter: (payload: { taxonId: number }) => dispatch({ type: 'deleteFilter', payload }),
      setOrganismFieldValue: (payload: OrganismFieldChangedEvent) =>
        dispatch({ type: 'setOrganismFieldValue', payload }),
      loadSample: (payload: Sample) => dispatch({ type: 'loadSample', payload }),
    }),
    [api]
  )
  return [state, actions]
}
