import React from 'react'
import { RouteWrapper as SampleTaxonomyIdentification } from '../SampleTaxonomyIdentification'
import { AppHeader } from '../AppHeader'
import { Outlet } from 'react-router-dom'

export const routes = [
  {
    path: '/',
    element: (
      <div css={{ height: '100vh', display: 'flex', flexDirection: 'column' }}>
        <AppHeader />
        <div css={{ flex: 1, padding: '0.5em' }}>
          <Outlet />
        </div>
      </div>
    ),
    children: [
      { path: 'samples', element: <div>/samples</div> },
      { path: 'samples/:sampleId', element: <div>sample</div> },
      {
        path: 'samples/:sampleId/taxonomy',
        element: <SampleTaxonomyIdentification />,
      },
      {
        path: 'samples/:sampleId/sort',
        element: <div>sample sort</div>,
      },
    ],
  },
]

