import { selector } from 'recoil'

import { apiState } from './api'

export const taxaState = selector({
  key: 'taxa',
  get: async ({ get }) => {
    const response = await get(apiState).fetchTaxa()
    return response
  },
})

export const lifeStagesState = selector({
  key: 'lifeStages',
  get: async ({ get }) => {
    const response = await get(apiState).fetchLifeStages()
    return response
  },
})

export const noteTypesState = selector({
  key: 'noteTypes',
  get: async ({ get }) => {
    const response = await get(apiState).fetchNoteTypes()
    return response
  },
})
