import React, { Suspense, useEffect, useState } from 'react'
import { useParams } from 'react-router'
import { useRecoilValue } from 'recoil'

import { Sample } from '../../types'
import { taxaState, lifeStagesState, noteTypesState } from '../../data'
import { useApi } from '../../data/api'
import { SampleTaxonomyIdentification } from './SampleTaxonomyIdentification'

const fallback = <div>loading...</div>

const Component = (): JSX.Element => {
  const taxa = useRecoilValue(taxaState)
  const lifeStages = useRecoilValue(lifeStagesState)
  const noteTypes = useRecoilValue(noteTypesState)

  const api = useApi()
  const [sample, setSample] = useState<Sample>()
  const { sampleId } = useParams()

  useEffect(() => {
    // TODO: do this Recoil-style with an uncached selector
    ;(async () => {
      const fetchedSample = await api.fetchSampleById(Number(sampleId))
      if (fetchedSample) setSample(fetchedSample)
    })()
    return () => setSample(undefined)
  }, [sampleId])

  if (!sample) return fallback // TODO: see above

  return <SampleTaxonomyIdentification taxa={taxa} lifeStages={lifeStages} noteTypes={noteTypes} sample={sample} />
}

export const RouteWrapper = (): JSX.Element => (
  <Suspense fallback={fallback}>
    <Component />
  </Suspense>
)
