/** @jsxImportSource @emotion/react */
import React, { useMemo, useRef, useState, useEffect, useCallback } from 'react'
import { useHotkeys } from 'react-hotkeys-hook'
import MuiChip from '@mui/material/Chip'

import { DataGrid, DataGridApis } from '../DataGrid'
import { TaxonomyField, PresentableTaxonOption } from '../TaxonomyField'
import { Taxon, LifeStage, NoteType, Sample, Organism } from '../../types'
import { useEditSampleReducer, EditSampleActions } from './editSampleReducer'
import { organismsColumnDefs } from './columnDefs'
import { InfoPanel } from './InfoPanel'
import { ConfirmDialog } from '../ConfirmDialog'
import * as renderers from './renderers'

interface EditSampleViewProps {
  taxa: Taxon[]
  lifeStages: LifeStage[]
  noteTypes: NoteType[]
  sample: Sample
}

export interface OrganismsDataGridContext {
  lifeStages: LifeStage[]
  noteTypes: NoteType[]
  actions: EditSampleActions
  setDeleteCandidateOrganism: (organism: OrganismRow) => void
}

const gridOptions = {
  stopEditingWhenCellsLoseFocus: true,
}

export interface OrganismRow extends Organism {
  taxonName: string
  levelName: string
  levelRankOrder: number
  lifeStageRankOrder: number
  canDuplicate: boolean
  canFilter: boolean
  canDelete: boolean
  allowedLifeStages: LifeStage[]
}

export const SampleTaxonomyIdentification = ({
  taxa,
  lifeStages,
  noteTypes,
  sample,
}: EditSampleViewProps): JSX.Element => {
  const [state, actions] = useEditSampleReducer()
  const [deleteCandidateOrganism, setDeleteCandidateOrganism] = useState<OrganismRow | null>(null)
  const [gridApis, setGridApis] = useState<DataGridApis>()
  const [isEditing, setIsEditing] = useState(false)

  const inputRef = useRef<HTMLInputElement | null>(null)

  useEffect(() => {
    if (!sample) return
    actions.loadSample(sample)
  }, []) // no dependencies

  const { organisms, organismFilters, isBugSizeRequired } = state

  useEffect(() => {
    if (!gridApis) return
    gridApis.columnApi.setColumnVisible('bugSize', isBugSizeRequired)
  }, [gridApis, isBugSizeRequired])

  useHotkeys('/', (e) => {
    e.preventDefault()
    e.stopPropagation()
    inputRef.current?.focus()
  })

  const taxonOptions = useMemo<PresentableTaxonOption[]>(() => {
    const organismTaxonIds = organisms.map(({ taxonId }) => taxonId)
    const added = taxa
      .filter((taxon) => organismTaxonIds.includes(taxon.id))
      .map((taxon) => ({ ...taxon, status: 'inSample' as const }))
    const available = taxa
      .filter((taxon) => !organismTaxonIds.includes(taxon.id))
      .map((taxon) => ({ ...taxon, status: 'notInSample' as const }))
    return [...added, ...available].map((taxonOption) => ({
      ...taxonOption,
      canAdd: isEditing,
      canFilter: true,
      defaultAction: isEditing ? 'add' : 'filter',
    }))
  }, [taxa, organisms, isEditing])

  // DATA GRID CONTEXT

  const context = useMemo<OrganismsDataGridContext>(
    () => ({
      lifeStages,
      noteTypes,
      actions,
      setDeleteCandidateOrganism,
    }),
    [lifeStages, noteTypes, actions]
  )

  // ROWS
  const getTaxonById = useCallback((taxonId: number) => taxa.find(({ id }) => id === taxonId), [taxa])

  const organismFilterTaxons = useMemo<Taxon[]>(
    () =>
      organismFilters.reduce<Taxon[]>((acc, { taxonId }) => {
        const taxon = getTaxonById(taxonId)
        if (!taxon) return acc
        return [...acc, taxon]
      }, []),
    [organismFilters, getTaxonById]
  )

  const filterTaxonIds = useMemo<number[] | null>(() => {
    if (organismFilterTaxons.length === 0) return null
    return organismFilterTaxons.reduce(
      (acc, { id, descendantIds }) => [...acc, id, ...(descendantIds || [])],
      [] as number[]
    )
  }, [organismFilterTaxons])

  const rows = useMemo<OrganismRow[]>(() => {
    const filteredOrganisms = filterTaxonIds
      ? organisms.filter(({ taxonId }) => filterTaxonIds.includes(taxonId))
      : organisms

    const activeLifeStages = lifeStages.filter(({ isActive }) => isActive)

    return filteredOrganisms.map((organism) => {
      const taxon = getTaxonById(organism.taxonId)

      const taxonName = taxon?.name || '?'
      const levelName = taxon?.levelName || '?'
      const levelRankOrder = taxon?.levelRankOrder ?? -1

      const canDuplicate =
        isEditing && organisms.filter(({ taxonId }) => taxonId === organism.taxonId).length < activeLifeStages.length // TODO: update for sizes support
      const canFilter = !organismFilters.find(({ taxonId }) => taxonId === organism.taxonId)
      const canDelete = isEditing

      const usedLifeStageIds = organisms
        .filter(({ id, taxonId }) => taxonId === organism.taxonId && id !== organism.id)
        .map(({ lifeStageId }) => lifeStageId)
      const allowedLifeStages = activeLifeStages.filter(({ id }) => !usedLifeStageIds.includes(id))

      const lifeStageRankOrder = lifeStages.find(({ id }) => id === organism.lifeStageId)?.rankOrder ?? 0

      return {
        ...organism,
        taxonName,
        levelName,
        levelRankOrder,
        lifeStageRankOrder,
        canDuplicate,
        canFilter,
        canDelete,
        allowedLifeStages,
      }
    })
  }, [getTaxonById, filterTaxonIds, organisms, organismFilters, lifeStages, isEditing])

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <InfoPanel sample={sample} isEditing={isEditing} setIsEditing={setIsEditing} />
        <div>
          <TaxonomyField
            inputRef={inputRef}
            options={taxonOptions}
            onAddOrganism={(taxon) => {
              actions.addOrganism(taxon)
              if (filterTaxonIds && !filterTaxonIds.includes(taxon.id)) actions.addFilter({ taxonId: taxon.id })
            }}
            onAddFilter={(taxon) => actions.addFilter({ taxonId: taxon.id })}
          />
        </div>
        <div css={{ minHeight: '2em', marginTop: '0.74em' }}>
          {organismFilters.length === 0 && <MuiChip sx={{ margin: '0.2em' }} label="No filters" variant="outlined" />}
          {organismFilters.map((organismFilter) => {
            const taxon = getTaxonById(organismFilter.taxonId)
            return (
              <MuiChip
                key={taxon?.id}
                sx={{ margin: '0.2em', backgroundColor: 'lightblue' }}
                label={`${taxon?.levelName}: ${taxon?.name}`}
                onDelete={() => actions.deleteFilter(organismFilter)}
              />
            )
          })}
        </div>
        <div css={{ flex: 1 }}>
          <DataGrid
            rows={rows}
            columnDefs={organismsColumnDefs}
            frameworkComponents={renderers}
            context={context}
            gridOptions={gridOptions}
            onGridApisReady={setGridApis}
          />
        </div>
      </div>
      <ConfirmDialog
        title="Delete organism record?"
        isOpen={!!deleteCandidateOrganism}
        onClose={() => setDeleteCandidateOrganism(null)}
        onConfirm={() => {
          if (!deleteCandidateOrganism) return
          actions.deleteOrganism(deleteCandidateOrganism)
        }}
        confirmButtonLabel="Delete"
      >
        This cannot be undone.
      </ConfirmDialog>
    </>
  )
}
